.pagination-form {
  display: flex;
  gap: 1.5rem;
}

.pagination-list {
  display: flex;
    align-items: center;
    justify-content: center;
    height: 2rem;
    min-width: 2rem;
    border-radius: 0.5rem;
    border: 0.1rem solid rgb(149, 213, 178);
    background-color: rgb(116, 198, 157);
    transition: 1s;
}

.pagination-list:hover {
  cursor: pointer;
  background-color: rgb(166, 216, 191);
}

.pagination-list__active {
  border: 0.13rem solid gray;
}