.search {
  display: flex;
  gap: 0 1rem;
  justify-content: center;
  padding: 10% 0;
  width: 100%;
  background-image: url('../images/2827397.jpg');
  background-color: white; 
  background-size: contain;
  background-repeat: no-repeat;
  border-radius: 0.5rem;
  border: 0.1rem solid rgb(223, 223, 223);
  background-position: top;
}

.search-input {
  width: 35%;
  height: 2rem;
  border-radius: 0.5rem;
  border: 0.1rem solid rgb(223, 223, 223);
  outline: none;
}

.search-button {
  border-radius: 0.5rem;
  border: 0.1rem solid rgb(149, 213, 178);
  background-color: rgb(116, 198, 157);
  transition: 1s;
}

.search-button:hover {
  background-color: rgb(149, 213, 178);
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.2);;
  cursor: pointer;
}

.loading {
  width: 8rem;
  height: 8rem;
  border: 1.5rem solid rgb(226, 220, 220);
  border-top: 1.5rem solid rgb(116, 198, 157);
  border-radius: 50%;
  animation: spin 1.5s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
