.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  padding: 10px;
  width: 250px;
  gap: 10px;
  background-color:white;
  border: 1px solid rgb(226, 220, 220);
  border-radius: 0.5rem;
}

.card-image {
  width: 200px;
  height: 250px;
  border-radius: 0.5rem;
}

img {
  object-fit: cover;
}

.card-title {
  text-align: center;
  color: #16444e;
}

.card-description {
  font-size: 0.9rem;
}

.card-price {
  font-weight: bold;
  color: #16444e;
}