.search-list {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  gap: 1rem;
  margin: 1rem 0;
}

.item-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 0.5rem;
  padding: 0.5rem;
  background-color: white;
  border: 0.1rem solid rgb(226, 226, 226);
  border-radius: 1rem;
  transition: all 1s;
}

.item-card:hover {
  cursor: pointer;
  box-shadow: 0 0 10px 5px rgba(221, 221, 221, 1);
}

.card-views {
  margin: 0 auto;
  color: rgb(104, 104, 104);
}

.item-popup {
  position: fixed;
  display: flex;
  left: calc(50% - 12.5rem);
  top: calc(50% - 12.5rem);
  flex-direction: column;
  gap: 1rem;
  width: 25rem;
  background-color: white;
  padding-bottom: 1rem;
  z-index: 2;
  border-radius: 0.5rem;
}

.item-button {
  position: absolute;
  right: 0;
  top: 0;
  margin-top: -50px;
  margin-right: -40px;
  width: 52px;
  height: 52px;
  border: 2px solid rgb(149, 213, 178);
  border-radius: 100px;
  background-color: rgb(149, 213, 178);
  background-image: url('../images/Vector.svg');
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

.item-button:hover {
  cursor: pointer;
}

.popup-image {
  width: 100%;
  height: 20rem;
  margin: 0 auto;
  object-fit:scale-down;
}

.popup-title,
.popup-description,
.popup-owner,
.popup-views {
  padding-left: 0.4rem;
  word-wrap: break-word;
}

.item-popup__hide {
  display: none;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(41, 41, 41, 0.6);
  z-index: 1;
}

.overlay-hide {
  display: none;
}

.result-per-page, .sorting {
  display: flex;
  gap: 0.5rem;
  align-items: center;
  flex-direction: column;
}

.button-per-page  {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2rem;
  height: 2rem;
  border-radius: 0.5rem;
  border: 0.1rem solid rgb(149, 213, 178);
  background-color: rgb(116, 198, 157);
  transition: 1s;
}

.sorting_select {
  border: 0.1rem solid rgb(149, 213, 178);
  background-color: rgb(116, 198, 157);
  border-radius: 0.5rem;
  height: 2rem;
}

.button-per-page:hover {
  cursor: pointer;
  background-color: rgb(166, 216, 191);
}

.button-per-page__active {
  border: 0.13rem solid gray;
}

.result-per-page > span, .sorting_title {
  font-size: 0.8rem;
}

.result-search {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  justify-content: space-around;
}

.filters {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 2rem;
  width: 100%;
}

.per-page-buttons {
  display: flex;
  gap: 0.5rem;
}
    